.footer {
  padding: 40px 0;
  background-color: var(--color-black);

  @include min-mobile {
    padding-top: 40px;
    padding-bottom: 20px;
  }

  &__container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    @include min-mobile {
      align-items: center;
      padding: 0 85px;
    }
  }

  &__left {
    display: flex;
    justify-content: space-between;

    @include min-mobile {
      flex-direction: column;
      align-items: center;
    }
  }

  &__logo {
    display: inline-flex;
    flex-direction: column;
    justify-content: center;
    margin-bottom: 5px;
    height: 48px;

    @include min-mobile {
      margin-bottom: 20px;
      width: 136px;
    }
  }

  &__tel {
    display: block;
    font-weight: 600;
    font-size: 30px;
    line-height: 160%;
    color: var(--color-white);
    transition: color .3s ease-in-out;

    &:focus {
        color: var(--accent-color);
      }

    &:hover {
        color: var(--accent-color);
      }

      @include min-mobile {
        margin-bottom: 40px;
        font-size: 16px;
        text-align: center;
      }
  }

  &__copy {
    margin: 0;
    font-weight: 400;
    font-size: 16px;
    line-height: 160%;
    color: rgba(255, 255, 255, 0.7);

    @include min-mobile {
      font-size: 14px;
    }
  }
}
