.trainers {

  @include min-mobile {
    padding: 60px 0;
  }

  &__wrapper {
    padding-left: max(15px, calc((100% - var(--content-width)) / 2));
  }

  &__title {
    margin: 0;
    margin-bottom: 60px;
    font-weight: 600;
    font-size: 50px;
    line-height: 160%;
    color: var(--color-black);

    @include min-mobile {
      margin-bottom: 40px;
      font-size: 30px;
      line-height: 140%;
      text-align: center;
    }
  }

  &__image {
    margin-bottom: 20px;
    border-radius: 20px;
    height: 500px;

    @include min-mobile {
      width: 220px;
      height: 306px;
    }
  }

  &__btn {
    font-weight: 400;
    font-size: 18px;
    line-height: 160%;
    color: var(--accent-color);
  }

  &__nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    cursor: pointer;
  }

  &__scroll {
    width: 360px;
    height: 5px;
    border-radius: 40px;
    background: rgba(20, 16, 36, 0.08);
  }

  &__btns {
    display: flex;

    @include min-mobile {
      display: none;
    }
  }

}

.trainers__item {
  &-title {
    margin-bottom: 10px;
    font-weight: 600;
    font-size: 24px;
    line-height: 160%;
    color: var(--color-black);

    @include min-mobile {
      font-size: 20px;
    }
  }

  &-pos {
    margin-bottom: 20px;
    font-weight: 400;
    font-size: 18px;
    line-height: 160%;
    color: rgba(20, 16, 36, 0.7);

    @include min-mobile {
      font-size: 14px;
    }
  }

  @include min-mobile {
    width: 220px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
}

.swiper-wrapper {
  margin-bottom: 60px;

  @include min-mobile {
    margin-bottom: 41px;
  }
}

.swiper-slide {
  width: 360px;

  @include min-mobile {
    margin-right: 20px;
    width: 220px;
  }
}

.swiper-scrollbar-drag {
  min-width: 260px;
  height: 5px;
  border-radius: 40px;
  background: var(--color-black);

  @include min-mobile {
    max-width: 140px;
    min-width: 0;
  }
}
