.header {
  position: absolute;
  top: 0;
  left: 0;
  padding: 30px 0;
  width: 100%;
  z-index: 5;

  @include mobile {
    padding: 10px 0;
  }

  &__container {
    @include flex-all-sb;

    @include mobile {
      padding: 0 15px;
    }
  }

  &__tel {
    font-weight: 600;
    font-size: 30px;
    line-height: 160%;
    color: var(--color-white);
    transition: color .3s ease-in-out;

    &:focus {
      color: var(--accent-color);
    }

    &:hover {
      color: var(--accent-color);
    }

    &-burger {
      display: flex;
      justify-content: center;
      align-items: center;
      display: none;
      width: 44px;
      height: 44px;
      border-radius: 10px;
      border: 2px solid rgba(255, 255, 255, 0.08);

      svg {
        width: 24px;
        height: 24px;
        fill: var(--color-white);
      }

      @include mobile {
        display: flex;
      }
    }

    @include small-tablet {
      display: none;
    }
  }

  &__burgers {
    display: flex;
    display: none;
    gap: 10px;

    @include small-tablet {
      display: flex;
    }
  }
}

.nav {

  &__list {
    display: flex;
  }

  &__item {
    &:not(:last-child) {
      margin-right: 40px;
    }
  }

  &__link {
    font-weight: 400;
    font-size: 20px;
    line-height: 160%;
    color: var(--color-white);
    transition: color .3s ease-in-out;

    &:focus {
        color: var(--accent-color);
      }

      &:hover {
        color: var(--accent-color);
      }
  }

  @include big-desktop {
    display: none;
  }
}

.burger {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-content: center;
  justify-content: center;
  display: none;
  width: 44px;
  height: 44px;
  border-radius: 10px;
  border: 2px solid rgba(255, 255, 255, 0.08);

  &__line {
    display: inline-block;
    border: 1px solid #fff;
    width: 26px;
    border-radius: 10px;
    background-color: var(--color-white);

    &-small {
      width: 13px;
    }

    &:not(:last-child) {
      margin-bottom: 5px;
    }
  }

  @include big-desktop {
    display: flex;
  }
}

.logo {
  @include mobile {
    width: 136px;
  }
}
