.curse {
  background: #f9f9f9;

  @include min-mobile {
    padding-top: 40px;
    padding-bottom: 52px;
  }

  &__title {
    margin: 0;
    margin-bottom: 60px;
    font-weight: 600;
    font-size: 50px;
    line-height: 160%;
    color: var(--color-black);

    @include min-mobile {
      margin-bottom: 40px;
      font-size: 30px;
      line-height: 140%;
      text-align: center;
    }
  }

  &__list {
    grid-template-columns: repeat(12, 1fr);
    gap: 40px;

    @include min-mobile {
      gap: 30px;
    }
  }

  &__item {
    display: flex;
    flex-direction: column;
    justify-content: center;
    grid-column: 4 span;

    @include min-mobile {
      align-items: center;
      grid-column: 12 span;
      max-width: 300px;
    }

    &-title {
      display: inline-flex;
      align-items: center;
      margin: 0;
      margin-bottom: 20px;
      font-weight: 600;
      font-size: 24px;
      line-height: 160%;
      color: var(--color-black);

      @include min-mobile {
        align-items: normal;
        font-size: 20px;
      }
    }

    &-descr {
      margin: 0;
      max-width: 348px;
      font-weight: 400;
      font-size: 18px;
      line-height: 160%;
      color: rgba(20, 16, 36, 0.7);

      @include min-mobile {
        font-size: 16px;
        text-align: center;
      }
    }
  }
}

.tooltip {
  margin-left: 10px;

  &__btn {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 24px;
    height: 24px;
    border-radius: 100%;
    background-color: rgba(20, 16, 36, 0.08);

    svg {
      width: 3px;
      height: 13px;
    }

    &:focus {
      +.tooltip__txt {
          opacity: 1;
          visibility: visible;
        }
    }

    @include hover {
      + .tooltip__txt {
        opacity: 1;
        visibility: visible;
      }
    }
  }

  &__txt {
    border-radius: 5px;
    padding: 15px 20px;
    font-weight: 400;
    font-size: 16px;
    line-height: 160%;
    text-align: center;
    color: #141024;
    box-shadow: 0 2px 8px 0 rgba(20, 16, 36, 0.1);
    background: var(--color-white);
    opacity: 0;
    visibility: hidden;
    transition: opacity .3s ease-in-out, visibility .3s ease-in-out;
  }
}
