.contacts {
  position: relative;
  padding: 90px 0;
  background-color: var(--accent-color);

  @include min-mobile {
    padding: 40px 0;
  }

  &::before {
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 135px;
    background-image: url(../img/contacts-bg.png);
    background-position: bottom;
    background-size: cover;
  }

  &__container {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 144px;

    @include tablet {
      flex-direction: column;
    }

    @include min-mobile {
      gap: 40px;
    }
  }

  &__content {
    margin-left: 60px;

    @include tablet {
      display: flex;
      flex-direction: column;
      align-items: center;
      margin-left: 0;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 20px;
    font-weight: 600;
    font-size: 36px;
    line-height: 130%;
    color: var(--color-white);

    @include min-mobile {
      margin-bottom: 10px;
      font-size: 30px;
      line-height: 140%;
      text-align: center;
    }
  }

  &__date {
    margin: 0;
    font-weight: 400;
    font-size: 20px;
    line-height: 130%;
    color: var(--color-white);

    @include min-mobile {
      font-size: 16px;
      line-height: 160%;
      text-align: center;
    }
  }

  &__form {
    display: flex;
    flex-direction: column;
    margin-right: 60px;

    @include min-mobile {
      margin-right: 0;
    }

    &-field {
      margin-bottom: 10px;
      padding-left: 20px;
      width: 500px;
      height: 64px;
      border-radius: 5px;
      font-weight: 400;
      font-size: 18px;
      color: rgba(40, 41, 68, 0.7);
      background-color: #f6f6f6;
      outline: none;
      border: none;

      @include min-mobile {
        width: 300px;
      }
    }

    &-btn {
      padding: 19px 0;
      font-weight: 400;
      font-size: 18px;
      text-align: center;
      color: var(--color-white);
      border-radius: 5px;
      background: var(--color-black);
      z-index: 1000;
      transition: background .3s ease-in-out;

      &:hover {
        background: var(--accent-color);
      }
    }
  }
}
