.info {
  background-color: var(--color-white);

  @include mobile {
    padding: 60px 0;
  }

  &__content {
    margin-bottom: 60px;
    max-width: 891px;

    @include mobile {
    margin-bottom: 40px;
    max-width: 300px;
  }
}

&__descr {
  margin: 0;
  font-size: 30px;
  line-height: 160%;
  color: var(--color-black);


  span {
    font-weight: 600;
  }

  @include mobile {
    font-size: 20px;
  }
}

  &__list {
    grid-template-columns: repeat(12, 1fr);
    gap: 40px;

    @include mobile {
      gap: 30px;
    }
  }

  &__item {
    grid-column: 3 span;

    @include for-desktop {
      grid-column: 6 span;
    }

    @include mobile {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      grid-column: 12 span;
      max-width: 270px;
    }
  }

  &__num {
    font-weight: 600;
    font-size: 70px;
    line-height: 160%;
    color: var(--accent-color);

    @include mobile {
      font-size: 60px;
    }
  }

  &__text {
    margin: 0;
    font-size: 18px;
    line-height: 160%;
    color: rgba(20, 16, 36, 0.7);

    @include mobile {
      font-size: 16px;
      text-align: center;
    }
    }
  }
