.hero {
  position: relative;
  min-height: 1080px;
  background-color: var(--color-black);
  background-image: url(../img/hero-bg.png);
  background-position: right 213px bottom;
  background-size: 783px 917px;
  background-repeat: no-repeat;

  @include full-desktop {
    background-image: none;
  }

  @include big-desktop {
    min-height: 740px;
  }

  @include mobile {
    min-height: 630px;
    background-image: url(../img/hero-bg-mob.png);
    background-position: center bottom 55px;
    background-size: 273px 320px;
    background-repeat: no-repeat;
  }

  &__container {
    display: flex;
    flex-direction: column;
    align-items: flex-start;

    @include big-desktop {
      align-items: center;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 20px;
    padding-top: 344px;
    font-weight: 600;
    font-size: 70px;
    line-height: 160%;
    text-transform: uppercase;
    color: var(--color-white);

    @include big-desktop {
      padding-top: 200px;
    }

    @include mobile {
      margin-bottom: 10px;
      padding-top: 101px;
      font-size: 30px;
    }
  }

  &__subtitle {
    margin: 0;
    margin-bottom: 50px;
    max-width: 478px;
    font-size: 20px;
    line-height: 160%;
    color: var(--color-white);

    @include mobile {
      margin-bottom: 280px;
      font-size: 16px;
      text-align: center;
    }
  }

  &__btn {
    font-weight: 400;
    font-size: 18px;
    line-height: 160%;
    color: var(--color-white);

    @include mobile {
      width: 100%;
      font-size: 16px;
    }
  }
}
