.tariffs {
  padding-top: 0;

  @include min-mobile {
    padding-bottom: 60px;
  }

  &__title {
    margin: 0;
    margin-bottom: 80px;
    font-weight: 600;
    font-size: 50px;
    line-height: 160%;
    color: var(--color-black);

    @include min-mobile {
      margin-bottom: 40px;
      font-size: 30px;
      line-height: 140%;
      text-align: center;
    }
  }

  &__list {
    grid-template-columns: repeat(12, 1fr);
    gap: 40px;

    @include small-tablet {
      justify-items: center;
    }

    @include min-mobile {
      justify-items: start;
    }
  }

  &__item {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-column: 4 span;
    padding: 40px;
    border-radius: 20px;
    border: 1px solid rgba(20, 16, 36, 0.2);
    transition: background .3s ease-in-out, transform .3s ease-in-out;

    @include small-tablet {
      grid-column: 6 span;
    }

    @include min-mobile {
      grid-column: 12 span;
      padding: 40px 20px;
      min-width: 300px;
    }

    &-subtitle {
      display: flex;
      align-items: center;
      margin: 0;
      margin-bottom: 63px;
      font-weight: 600;
      font-size: 30px;
      line-height: 160%;
      text-align: center;
      color: var(--color-black);
      transition: color .3s ease-in-out;

      &::before {
        content: '';
        display: block;
        margin-right: 10px;
        width: 20px;
        height: 3px;
        background-color: currentColor;
      }

            &::after {
              content: '';
              display: block;
              margin-left: 10px;
              width: 20px;
              height: 3px;
              background-color: currentColor;
            }

            &--current {
              color: var(--color-white);
            }
    }

    &-price {
      display: inline-flex;
      margin-bottom: 20px;
      font-weight: 600;
      font-size: 50px;
      line-height: 160%;
      text-align: center;
      color: var(--color-black);
      transition: color .3s ease-in-out;

      &--current {
          color: var(--color-white);
        }
    }

    &-descr {
      margin: auto;
      font-weight: 400;
      font-size: 18px;
      line-height: 160%;
      text-align: center;
      color: var(--color-black);
      transition: color .3s ease-in-out;

      &--current {
          color: var(--color-white);
        }

        @include min-mobile {
          font-size: 16px;
        }
    }

    &--current {
      border: 1px solid var(--color-black);
      background-color: var(--color-black);
      transform: scale(1.1);

      @include min-mobile {
        max-width: 300px;
      }
    }
  }


  &__btn {
    margin-top: 61px;
    width: 100%;
    font-weight: 400;
    font-size: 18px;
    line-height: 160%;
    text-align: center;
    color: var(--color-white);

    @include for-desktop {
      font-size: 14px;
    }

    @include tablet {
      font-size: 9px;
    }

    @include min-mobile {
      font-size: 16px;
    }
  }
}
