.graph-modal__close {
  top: 40px;
  right: 40px;
  width: auto;
  height: auto;
  font-weight: 400;
  font-size: 18px;
  line-height: 160%;
  text-align: right;
  color: rgba(20, 16, 36, 0.7);
  background-image: none;
}

.poppup {
  padding: 40px;

  &__top {
    display: flex;
    margin-bottom: 40px;
  }

  &__img {
    margin-right: 40px;
  }

  &__name {
    margin-bottom: 10px;
    font-weight: 600;
    font-size: 24px;
    line-height: 160%;
    color: var(--color-black);
  }

  &__post {
    margin-bottom: 10px;
    font-weight: 400;
    font-size: 18px;
    line-height: 160%;
    color: rgba(20, 16, 36, 0.7);
  }

  .social {
    display: flex;
    gap: 10px;

    &__link {
      width: 30px;
      height: 30px;
    }

    svg {
      display: block;
      width: 30px;
      height: 30px;
      object-fit: cover;
    }
  }

  &__bottom {
    &-content {
      padding-top: 20px;
      max-height: 375px;
    }
  }

  &__descr {
    margin-bottom: 40px;
  }

  &__subtitle {
    margin: 0;
    margin-bottom: 20px;
    font-weight: 600;
    font-size: 24px;
    line-height: 160%;
    color: var(--color-black);
  }

  &__text {
    margin: 0;
    margin-bottom: 5px;
    font-weight: 400;
    font-size: 20px;
    line-height: 160%;
    color: var(--color-black);
  }
}

.simplebar-track.simplebar-vertical {
  margin-top: 20px;
  margin-bottom: 20px;
  border-radius: 40px;
  width: 5px;
  background: rgba(20, 16, 36, 0.08);
}

.simplebar-scrollbar:before {
  left: 0;
  right: 0;
  width: 5px;
  border-radius: 40px;
  background: var(--color-black);
  opacity: 1;
}

.tabs {
  &__menu {
    padding-bottom: 20px;
    border-bottom: 1px solid rgba(20, 16, 36, 0.11);
  }



  &__nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 70%;

    &-btn {
      font-weight: 400;
      font-size: 20px;
      line-height: 160%;
      color: var(--color-black);

      &:focus {
        color: var(--accent-color);
          outline: none;
          background-color: transparent;
      }

      &--active {
        color: var(--accent-color);
        outline: none;
        background-color: transparent;
      }
    }
  }
}
